<template>
	<div class="live-keep-live2keep-all-success">
		<mt-cell title="提示信息"></mt-cell>
		<div class="success-contain">
			<mt-cell title="交易成功！"></mt-cell>
			<mt-cell title="客户姓名：" :value="detail.name"></mt-cell>
			<mt-cell title="活期账户：" :value="detail.liveAccount"></mt-cell>
			<mt-cell title="销户金额：" :value="detail.money1"></mt-cell>
			<mt-cell title="账户余额：" :value="detail.money2"></mt-cell>
			<mt-cell title="币种：" :value="detail.type"></mt-cell>
			<mt-cell title="存期：" :value="detail.date"></mt-cell>
			<mt-cell title="开户日期：" :value="detail.date1"></mt-cell>
			<mt-cell title="起息日期：" :value="detail.date2"></mt-cell>
			<mt-cell title="执行利率：" :value="detail.rate"></mt-cell>
			<mt-cell title="活期利息" :value="detail.interest1"></mt-cell>
			<mt-cell title="定期利息：" :value="detail.interest2"></mt-cell>
			<mt-cell title="提取方式：" :value="detail.giveType"></mt-cell>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				name: '',
				liveAccount: '',
				money1: '',
				money2: '',
				type: '',
				date: '',
				date1: '',
				date2: '',
				rate: '',
				interest1: '',
				interest2: '',
				giveType: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/live-keep/keep2live');
		},
		successKeep2live(account) {
			service.successKeep2live({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.successKeep2live(this.$route.params.account);
	}
};

</script>

<style lang="scss" scoped>

.live-keep-live2keep-all-success{
	.mint-cell:after{
		height: 0;
	}
	.success-contain{
		border-top: 1px solid #d9d9d9;
		border-bottom: 1px solid #d9d9d9;
	}
}

</style>


