<template>
	<div class="live-keep-live2keep-live">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="网通活期账号：" placeholder="" v-model="account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value="活期转定活两便"></submit-button>
		</div>
        <div class="field-warn">
            <ol>温馨提示：
                <li>1、定活两便50元起存。</li>
                <li>2、开户成功后，您可以在交易明细中查询定期账户。</li>
                <li>3、支取时按同档次整存整取利率打六折计算利息，执行存期档次分为三个月、六个月、一年。</li>
                <li>4、如有疑问，请致电96669。</li>
            </ol>
        </div>
	</div>
</template>

<script>

export default{
	data() {
		return {
			account: '',
            submitStatus: false,
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
    watch: {
        account: function(val) {
            if(val){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.$router.push(`/live-keep/live2keep/live/detail/${this.account}`);
        },
        back() {
        	history.back();
        }
	},
	created() {
		this.account = this.account || this.$store.state.live2keepLive.account;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateLive2keepLive', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateLive2keepLive', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.live-keep-live2keep-live{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


