<template>
	<div class="live-keep-live2keep-all-detail">
		<mt-cell title="活期账户信息"></mt-cell>
		<mt-cell title="活期账号：" :value="account.number"></mt-cell>
		<mt-cell title="可用余额：" :value="account.money"></mt-cell>
		<mt-cell title="币种：" :value="account.type"></mt-cell>
		<mt-cell title="存期信息"></mt-cell>
		<mt-cell class="mint-select" title="存期：" is-link>
            <select @change="getKeepRate(detail.date)" class="mint-field-core" v-model="detail.date">
                <option v-for="item in seletsItem.dates" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
		<mt-cell title="执行利率：" :value="detail.rate"></mt-cell>
		<mt-cell class="mint-select" title="转存标志：" is-link>
            <select class="mint-field-core" v-model="detail.type">
                <option v-for="item in seletsItem.types" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
		<mt-field type="number" v-model="detail.money" label="交易金额："></mt-field>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<submit-button @submit="submit" :submitStatus="submitStatus" value="提交"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			account: {
				number: '',
				money: '',
				type: ''
			},
			detail: {
				date: '3',
				rate: '',
				type: '0',
				money: ''
			},
			submitStatus: false,
			seletsItem: {
				dates: [
					{
						val: '3',
						desc: '三个月'
					},
					{
						val: '6',
						desc: '六个月'
					},
					{
						val: '12',
						desc: '一年'
					},
					{
						val: '24',
						desc: '两年'
					},
					{
						val: '36',
						desc: '三年'
					},
					{
						val: '60',
						desc: '五年'
					}
				],
				types: [
					{
						val: '0',
						desc: '本息转存'
					},
					{
						val: '1',
						desc: '不转存'
					}
				]
			},
			cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
	watch: {
		detail: {
			handler: function(item){
				if(item.date && item.rate && item.type && item.money){
					this.submitStatus = true;
				}else{
					this.submitStatus = false;
				}
			},
			deep: true
		}
	},
	methods: {
		submit() {
			service.submitLive2keep()
			.then(data => {
				this.$router.push(`/live-keep/live2keep/all/success/${this.account.number}`);
			});
		},
		back() {
			history.back();
		},
		getAccountDetail(account) {
			service.getAccountDetail({
				account: account
			}).then(data => {
				this.account = data.data.data;
			});
		},
		getKeepRate(days) {
			service.getKeepRate({
				days: days
			}).then(data => {
				this.detail.rate = data.data.data;
			});
		}
	},
	created() {
		this.getAccountDetail(this.$route.params.account);
		this.getKeepRate(this.detail.date);
	}
};

</script>


