<template>
	<div class="live-keep-keep2live-list">
		<mt-cell class="product-title" title="账户信息"></mt-cell>
		<div class="products">
			<a @click="detail(item.id)" v-for="item in products" class="product-item" href="javascript:;">
				<mt-cell title="类型：" :value="item.type | keepType"></mt-cell>
				<mt-cell title="子账号：" :value="item.account"></mt-cell>
				<mt-cell title="余额：" :value="item.money | number(2) + '元'"></mt-cell>
				<mt-cell title="期限：" :value="item.date"></mt-cell>
				<mt-cell title="利率：" :value="item.rate| number(8) + '%'"></mt-cell>
				<mt-cell title="起息日期：" :value="item.date1"></mt-cell>
				<mt-cell title="到期日期：" :value="item.date2"></mt-cell>
				<mt-cell v-show="item.type == 1" title="转存标志：" :value="item.status"></mt-cell>
			</a>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default{
	data() {
		return {
			products: []
		};
	},
	methods: {
		detail(id) {
			this.$router.push(`/live-keep/keep2live/detail/${id}`);
		},
		back() {
			history.back();
		},
		getKeepListForAccount(account) {
			service.getKeepListForAccount({
				account: account
			}).then(data => {
				this.products = data.data.data;
			});
		}
	},
	created() {
		this.getKeepListForAccount(this.$route.params.account);
	}
};

</script>

<style lang="scss">
	
.live-keep-keep2live-list{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


