<template>
	<div class="live-keep-live2keep-keep-success">
		<mt-cell title="提示信息"></mt-cell>
		<div class="success-contain">
			<mt-cell title="交易成功！"></mt-cell>
			<mt-cell title="活期账户：" :value="detail.liveAccount"></mt-cell>
			<mt-cell title="定期账户：" :value="detail.keepAccount"></mt-cell>
			<mt-cell title="交易金额：" :value="detail.money"></mt-cell>
			<mt-cell title="存期：" :value="detail.date"></mt-cell>
			<mt-cell title="开户日期：" :value="detail.date1"></mt-cell>
			<mt-cell title="起息日期：" :value="detail.date2"></mt-cell>
			<mt-cell title="到期日期：" :value="detail.date3"></mt-cell>
			<mt-cell title="执行利率：" :value="detail.rate"></mt-cell>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				liveAccount: '',
				keepAccount: '',
				money: '',
				date: '',
				date1: '',
				date2: '',
				date3: '',
				rate: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/live-keep/live2keep');
		},
		successLive2keepKeep(account) {
			service.successLive2keepKeep({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.successLive2keepKeep(this.$route.params.account);
	}
};

</script>

<style lang="scss" scoped>

.live-keep-live2keep-keep-success{
	.mint-cell:after{
		height: 0;
	}
	.success-contain{
		border-top: 1px solid #d9d9d9;
		border-bottom: 1px solid #d9d9d9;
	}
}

</style>


