<template>
	<div class="live-keep-live2keep-keep-detail">
		<mt-cell title="活期账户信息"></mt-cell>
		<mt-cell title="活期账号：" :value="account.number"></mt-cell>
		<mt-cell title="可用余额：" :value="account.money"></mt-cell>
		<mt-cell title="币种：" :value="account.type"></mt-cell>
		<mt-cell title="存期信息"></mt-cell>
		<mt-cell class="mint-select" title="交易类型：" is-link>
            <select class="mint-field-core" v-model="detail.type">
                <option v-for="item in seletsItem.types" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
        <mt-cell v-show="detail.type == 1" @click.native="showSelectorPaymentAccount()" title="定期账户" placeholder="" v-model="detail.account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<mt-cell v-show="detail.type != 1" class="mint-select" title="存期：" is-link>
            <select @change="getKeepRate(detail.date)" class="mint-field-core" v-model="detail.date">
                <option v-for="item in seletsItem.dates" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
        <mt-cell v-show="detail.type == 1" title="存期：" :value="detail.date"></mt-cell>
		<mt-cell title="执行利率：" :value="detail.rate"></mt-cell>
		<mt-field type="number" v-model="detail.money" label="交易金额："></mt-field>
		<mt-cell v-show="detail.type == 1" title="账户状态：" :value="detail.status"></mt-cell>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">提交</mt-button>
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			account: {
				number: '',
				money: '',
				type: ''
			},
			detail: {
				type: '0',
				account: '',
				date: '3',
				rate: '',
				money: '',
				status: ''
			},
			seletsItem: {
				types: [
					{
						val: '0',
						desc: '开户'
					},
					{
						val: '1',
						desc: '续存'
					}
				],
				dates: [
					{
						val: '3',
						desc: '三个月'
					},
					{
						val: '6',
						desc: '六个月'
					},
					{
						val: '12',
						desc: '一年'
					},
					{
						val: '24',
						desc: '两年'
					},
					{
						val: '36',
						desc: '三年'
					},
					{
						val: '60',
						desc: '五年'
					}
				]
			},
			cellPassword: {
                title: '交易密码：',
                rule: 'number'
            },
            selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505',
                        status: '已签约'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡',
                        status: '已签约'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金',
                        status: '已签约'
                    }
                ]
            }
		};
	},
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.account;
                this.detail.status = data.item.status;
            }, data => {
            });
        },
        validate() {
        	if(this.detail.type != 1){
        		if(!this.detail.money){
	        		Toast({
	                    message: '请输入交易金额！',
	                    iconClass: 'error',
	                    duration: 1000
	                });
	                return true;
        		}
        	}else{
        		var _error = [];
            	if(this.$validate.isEmpty(this.detail.account)) {
                	_error.push( '未选择定期账户' );
            	}
            	if(this.$validate.isEmpty(this.detail.money)){
            		_error.push('请输入交易金额！');
            	}
            	if( _error.length > 0 ) {
	                Toast({
	                    message: _error[0],
	                    iconClass: 'error',
	                    duration: 1000
	                });
                	return true;
            	}
        	}
        	return false;
        },
		submit() {
			if(this.validate()){
				return;
			}
			service.submitLive2keepKeep()
			.then(data => {
				this.$router.push(`/live-keep/live2keep/keep/success/${this.account.number}`);
			});
		},
		back() {
			history.back();
		},
		getAccountDetail(account) {
			service.getAccountDetail({
				account: account
			}).then(data => {
				this.account = data.data.data;
			});
		},
		getKeepRate(days) {
			service.getKeepRate({
				days: days
			}).then(data => {
				this.detail.rate = data.data.data;
			});
		}
	},
	created() {
		this.getAccountDetail(this.$route.params.account);
		this.getKeepRate(this.detail.date);
	}
};

</script>


