<template>
	<div class="live-keep-keep2live-detail">
		<mt-cell title="账户信息"></mt-cell>
		<mt-cell title="活期账号：" :value="detail.liveAccount"></mt-cell>
		<mt-cell title="定期账户：" :value="detail.keepAccount"></mt-cell>
		<mt-cell title="账户余额：" :value="detail.money"></mt-cell>
		<mt-cell title="币种：" :value="detail.type"></mt-cell>
		<mt-cell title="账户状态：" :value="detail.status"></mt-cell>
		<mt-cell v-show="detail.keepType != 2" title="存期：" :value="detail.date"></mt-cell>
		<mt-cell title="起息日期：" :value="detail.date1"></mt-cell>
		<mt-cell v-show="detail.keepType != 2" title="到期日期：" :value="detail.date2"></mt-cell>
		<mt-cell v-show="detail.keepType != 2" title="执行利率：" :value="detail.rate"></mt-cell>
		<mt-cell title="其他信息"></mt-cell>
		<mt-cell class="mint-select" title="提取方式：" is-link>
            <select class="mint-field-core" v-model="inputs.giveType" :disabled="detail.keepType != 2">
                <option v-for="item in giveTypes" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
        <mt-field v-show="inputs.giveType == 0" type="number" v-model="inputs.giveMoney" label="提取金额："></mt-field>
        <mt-field v-model="inputs.idCard" label="证件号码："></mt-field>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">提交</mt-button>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				liveAccount: '',
				keepAccount: '',
				money: '',
				type: '',
				status: '',
				date: '',
				date1: '',
				date2: '',
				rate: ''
			},
			inputs: {
				giveType: '0',
				giveMoney: '',
				idCard: ''
			},
			giveTypes: [
				{
					val: '0',
					desc: '部分提取'
				},
				{
					val: '1',
					desc: '全部提取'
				}
			],
			cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			if(this.detail.keepType != 2){
				let self = this;
	            MessageBox({
	                title: '提示信息',
	                message: '您的存款尚未到期，提前支取将按活期计息，确定继续？',
	                showCancelButton: true
	            }).then(action => {
	            	if(action === 'confirm'){
	            		self.submitKeep2live();
	            	}
	            });
			}else{
				this.submitKeep2live();
			}
		},
		submitKeep2live() {
			service.submitKeep2live()
			.then(data => {
				this.$router.push(`/live-keep/keep2live/success/${this.detail.account}`);
			});
		},
		back() {
			history.back();
		},
		getKeepDetail(account) {
			service.getKeepDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
				if(this.detail.keepType != 2){
					this.inputs.giveType = '1';
				}
			});
		}
	},
	created() {
		this.getKeepDetail(this.$route.params.account);
	}
};

</script>


